import React from 'react';

function DashboardPage() {
  const handleToggleSauna = async () => {
    const username = localStorage.getItem('username');
    const password = localStorage.getItem('password');
    const dyndns = localStorage.getItem('dyndns');

    if (!username || !password || !dyndns) {
      alert('Credentials fehlen.');
      return;
    }

    try {
      const response = await fetch(`http://${dyndns}/relay/0?turn=toggle`, {
        method: 'GET',
        headers: {
          'Authorization': 'Basic ' + btoa(`${username}:${password}`)
        }
      });

      if (!response.ok) {
        throw new Error(`HTTP ${response.status}`);
      }

      alert('Schaltvorgang erfolgreich!');
    } catch (error) {
      alert('Fehler beim Schalten: ' + error.message);
    }
  };

  return (
    <div className="setting-container">
      <div className="setting-box">
        <h2>Dashboard</h2>
        <button onClick={handleToggleSauna}>Sauna ein-/ausschalten</button>
      </div>
    </div>
  );
}

export default DashboardPage;
